addEventListener('DOMContentLoaded',function(){
    mobileMenu();
    activ();
});
function mobileMenu () {
    const btn_menu = document.querySelector('.icon_menu');
    if(btn_menu) {
        btn_menu.addEventListener('click',()=>{
            const menu_items = document.querySelector('.menu');
            menu_items.classList.toggle('active');
        });
    }
}
function activ() {
    const activePage = location.href;
    const menuItem = document.querySelectorAll('a');
    const menuLength = menuItem.length;
    for (let i =  0; i<menuLength; i++){
         if (menuItem[i].href === activePage){
            menuItem[i].className = "activ"
         }
    }
}

